@import url('//maxcdn.bootstrapcdn.com/font-awesome/4.3.0/css/font-awesome.min.css');
a {
    text-decoration: none;
  }
  
  nav {
      font-family: monospace;
      background-color: #46305D !important;
      color: #fff !important;
  }
  .navbar-brand{
      font-size: 33px !important;
  }
  li a{
      color: #fff !important ;
      font-size: 16px;
      font-weight: 600;
  }
.dropdown-menu li a{
    color: #808080 !important;
}
  .main{
      display: flex;
      justify-content: space-around;
      align-items: center;
  }
  .navbar-toggler-icon{
      border: none !important;
      color: #fff !important;
  }
  
  .head h3{
      font-size: 20px;
  }
  ul{
      display: flex;
      gap: 25px;
      list-style: none;
      color: #fff !important;
  }
  li{
      font-size: 14px;
     
  }
  body{
      background-color: #F8F8F9;
      margin: 0;
  }
.home{
    background: #ffffff;
    height: 550px;
    position: relative;
}
.home .child{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
  .headerbtn{
      display: flex;
      justify-content: center;
      gap: 20px;
  }
  button{
      background-color:#398AFF;
      color: white;
      padding: 5px 20px;
      border-radius: 5px ;
     border: none;
  }
  .headerbtn button:first-child{
      background-color: transparent;
    border: 1px solid #398AFF;
    color: #398AFF;
  }

  .rowdata{
      flex-wrap: wrap;
  }
  @media (max-width: 767px) {
    .half{
    width: 100% !important;
    }
    .rowdata,.headerbtn{
        flex-direction: column !important;
    }
    .child h1{
        font-weight: 700;
    }
    .headerbtn button{
        margin: 2px 40px 2px 40px;
        padding: 10px;
    }
    .home .child{
        width: 100%;
        padding: 5px 10px;
    }
    .fmain{
        flex-direction: column !important;
        justify-content:start;
       
    }
  }
  .searchContainer{
      display: flex;
      align-items: center;
      background-color: white;
      border: 1px solid #dadada;
  }
  .searchBox{
      outline: none;
      border: none;
  }
  .searchBox:focus{
      outline: none !important;
      border: none !important;
  }
  .searchIcon{
    padding: 5px 10px;
  }
  
  .navbar-nav li:hover > ul.dropdown-menu {
    display: block;
}
.dropdown-submenu {
    position:relative;
}
.dropdown-submenu > .dropdown-menu {
    top: 0;
    left: 100%;
    margin-top:-6px;
}

.dropdown-menu > li > a:hover:after {
    text-decoration: underline;
    transform: rotate(-90deg);
} 

@media screen and (min-width: 761) {

}
.footer{
    background-color: #2B2E3B ;
    margin-top: 90px !important;
}
.fcol{
    text-align: start;
    padding: 3px;
}
.fcol h3,.fcol h1{
    color: #fff;
}
.fcol p{
    margin: 0;
    color: rgb(211, 207, 207);
    text-align: start;
    padding: 3px;
    font-size: 16px;
}